import { Link, useNavigate } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import { adminLogin } from "api/adminAuth";
import Swal from "sweetalert2";
import { AdminContext } from "contexts/AdminContext";

// components
import AuthContainer from "components/containers/AuthContainer";
import Logo from "components/Logo";
import Title from "components/Title";
import Input from "components/Input";
import Button from "components/Button";

function AdminLoginPage() {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const [whichError, setWhichError] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");

  const { isAdminLogin, setIsAdminLogin } = useContext(AdminContext);
  const navigate = useNavigate();

  const handleAccountChange = (e) => {
    setAccount(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // 
    setWhichError([]);
    setErrorMessage("");

    // 
    if ([account, password].some((value) => value.length === 0)) {
      if (account.length === 0) {
        setWhichError((we) => [...we, "account"]);
      }

      if (password.length === 0) {
        setWhichError((we) => [...we, "password"]);
      }

      setErrorMessage("");
      // 
      return;
    }

    try {
      const { message, token } = await adminLogin({ account, password });

      // 
      if (!token) {
        setWhichError(["account", "password"]);
        setErrorMessage(message);
        return;
      }

      //  token 
      if (token) {
        //  token
        localStorage.setItem("adminToken", token);

        // 
        Swal.fire({
          position: "top",
          icon: "success",
          title: message,
          timer: 1500,
          showConfirmButton: false,
          customClass: {
            icon: "swalIcon right",
            title: "swalTitle",
          },
        });

        // 
        setIsAdminLogin(true);
        // 
        navigate("/admin/tweets");

        return;
      }
    } catch (error) {
      console.error(error);
    }
  };

  // 
  useEffect(() => {
    if (isAdminLogin) {
      navigate("/admin/tweets");
    }
  }, [isAdminLogin, navigate]);

  return (
    <AuthContainer>
      <Logo />
      <Title></Title>
      <form onSubmit={handleSubmit}>
        <Input
          id="login_account"
          label=""
          type="text"
          placeholder=""
          value={account}
          onChange={handleAccountChange}
          isError={whichError.some((which) => which === "account")}
          errorMessage={errorMessage}
        />
        <Input
          id="login_password"
          label=""
          type="password"
          placeholder=""
          value={password}
          onChange={handlePasswordChange}
          isError={whichError.some((which) => which === "password")}
          errorMessage={errorMessage}
        />
        <Button primary rounded className="w-full">
          
        </Button>
      </form>
      <div className="d-flex justify-content-end mt-3 p-2">
        <Link to="/login"></Link>
      </div>
    </AuthContainer>
  );
}

export default AdminLoginPage;
