export const deleteTweet = async (id) => {
  try {
    // 
    const res = await axiosInstance.delete(`${baseUrl}/tweets/${id}`);

    return res.data;
  } catch (error) {
    console.error(
      "[  Delete Tweet Failed ]:",
      error.response.data.message
    );

    return error.response.data;
  }
};
