import { Link, useNavigate } from "react-router-dom";
import { useContext, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { register } from "api/userAuth";
import { InfoContext } from "contexts/InfoContext";

// components
import AuthContainer from "components/containers/AuthContainer";
import Logo from "components/Logo";
import Title from "components/Title";
import Input from "components/Input";
import Button from "components/Button";

function RegisterPage() {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [whichError, setWhichError] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");

  const { isUserLogin } = useContext(InfoContext);

  const navigate = useNavigate();

  const handleAccountChange = (e) => {
    setAccount(e.target.value);
  };

  const handleNameChange = (e) => {
    // 
    setWhichError([]);
    setErrorMessage("");

    //   50 
    if (e.target.value.length > 50) {
      setWhichError(["name"]);
      setErrorMessage(" 50 ");
    }

    setName(e.target.value);
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleCheckPasswordChange = (e) => {
    setCheckPassword(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // 
    setWhichError([]);
    setErrorMessage("");

    // 
    if (
      [account, name, email, password, checkPassword].some(
        (value) => value.length === 0
      )
    ) {
      if (account.length === 0) {
        setWhichError((we) => [...we, "account"]);
      }

      if (name.length === 0) {
        setWhichError((we) => [...we, "name"]);
      }

      if (email.length === 0) {
        setWhichError((we) => [...we, "email"]);
      }

      if (password.length === 0) {
        setWhichError((we) => [...we, "password"]);
      }

      if (checkPassword.length === 0) {
        setWhichError((we) => [...we, "checkPassword"]);
      }
      setErrorMessage("");
      // 
      return;
    }

    // 
    if ([account, name].some((value) => value.trim().length === 0)) {
      if (account.trim().length === 0) {
        setWhichError((we) => [...we, "account"]);
      }
      if (name.trim().length === 0) {
        setWhichError((we) => [...we, "name"]);
      }

      setErrorMessage("");
      // 
      return;
    }

    //  50 
    if (name.length > 50) {
      setWhichError(["name"]);
      setErrorMessage(" 50 ");
      return;
    }

    //  OK 
    try {
      const { message } = await register({
        account,
        name,
        email,
        password,
        checkPassword,
      });

      // 
      if (message === "Account ") {
        setWhichError(["account"]);
        setErrorMessage(message);
        return;
      }

      // name  50 
      if (message === " 50 ") {
        setWhichError(["name"]);
        setErrorMessage(message);
        return;
      }

      // 
      if (message === "Email ") {
        setWhichError(["email"]);
        setErrorMessage(message);
        return;
      }

      // 
      if (message === "Email ") {
        setWhichError(["email"]);
        setErrorMessage(message);
        return;
      }

      // 
      if (message === "") {
        setWhichError(["password", "checkPassword"]);
        setErrorMessage(message);
        return;
      }

      // 
      if (message === "") {
        Swal.fire({
          position: "top",
          icon: "warning",
          title: message,
          timer: 1500,
          showConfirmButton: false,
          customClass: {
            icon: "swalIcon right",
            title: "swalTitle",
          },
        });
        return;
      }

      //  token 
      Swal.fire({
        position: "top",
        icon: "success",
        title: message,
        timer: 1500,
        showConfirmButton: false,
        customClass: {
          icon: "swalIcon right",
          title: "swalTitle",
        },
      });

      navigate("/login");
    } catch (error) {
      console.error(error);
    }
  };

  // useEffect
  // 
  useEffect(() => {
    if (isUserLogin) {
      navigate("/main");
    }
  }, [isUserLogin, navigate]);

  return (
    <AuthContainer>
      <Logo />
      <Title></Title>
      <form onSubmit={handleSubmit}>
        <Input
          id="input_account"
          label=""
          type="text"
          placeholder=""
          value={account}
          onChange={handleAccountChange}
          isError={whichError.some((which) => which === "account")}
          errorMessage={errorMessage}
        />
        <Input
          id="input_name"
          label=""
          type="text"
          placeholder="50"
          value={name}
          onChange={handleNameChange}
          isError={whichError.some((which) => which === "name")}
          errorMessage={errorMessage}
        />
        <Input
          id="input_email"
          label="Email"
          type="text"
          placeholder=" Email"
          value={email}
          onChange={handleEmailChange}
          isError={whichError.some((which) => which === "email")}
          errorMessage={errorMessage}
        />
        <Input
          id="input_password"
          label=""
          type="password"
          placeholder=""
          value={password}
          onChange={handlePasswordChange}
          isError={whichError.some((which) => which === "password")}
          errorMessage={errorMessage}
        />
        <Input
          id="input_checkPassword"
          label=""
          type="password"
          placeholder=""
          value={checkPassword}
          onChange={handleCheckPasswordChange}
          isError={whichError.some((which) => which === "checkPassword")}
          errorMessage={errorMessage}
        />
        <Button primary rounded className="w-full">
          
        </Button>
      </form>
      <div className="d-flex justify-content-center mt-4">
        <Link to="/login"></Link>
      </div>
    </AuthContainer>
  );
}

export default RegisterPage;
