import { useContext, useEffect, useState } from "react";
import styled from "styled-components";
import Swal from "sweetalert2";
import { postReply } from "api/tweetAuth";
import { TweetContext } from "contexts/TweetContext";
import { InfoContext } from "contexts/InfoContext";

// Components
import { ReactComponent as CrossFocus } from "assets/icons/cross_focus.svg";
import Button from "components/Button";

const StyledDiv = styled.div`
  width: 600px;
  height: 500px;
  position: relative;
  z-index: 3;
  border-radius: 20px;
  background-color: var(--white);

  .cross-box {
    padding: 15px;
    border-bottom: 1px solid #e6ecf0;
    cursor: pointer;

    &:hover {
      path {
        fill: var(--grey9);
      }
    }
  }
`;

const StyledContent = styled.div`
  padding: 25px;
  line-height: 1.5;

  img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
  }

  .side-line {
    margin: 15px 25px;
    width: 2px;
    height: 115px;
    background-color: var(--grey5);
  }

  .content-area {
    .title {
      .user-name {
        margin-right: 5px;
        font-weight: bold;
        font-size: 20px;
      }

      span {
        color: var(--secondary);
        font-size: 14px;
      }
    }

    .tweet-text {
      font-size: 16px;
      line-height: 26px;
      min-height: 130px;
    }

    .reply-to {
      margin-top: 5px;
      color: var(--secondary);
      font-size: 14px;

      .account {
        margin-left: 5px;
        color: var(--brand-color);
      }
    }
  }

  textarea {
    margin-top: 10px;
    width: 100%;
    height: 150px;
    padding: 10px;
    resize: none; //
    border: none;
    outline: none;
    font-size: 17px;
  }

  .hint {
    color: var(--grey7);
  }

  .error {
    color: var(--error);
  }
`;

function ReplyModal() {
  const [comment, setComment] = useState("");
  const [isEmpty, setIsEmpty] = useState(false);
  const {
    handleReplyClick,
    getSingleTweetAsync,
    tweet,
    setTweet,
    tweetId,
    tweetReplies,
    setTweetReplies,
    tweets,
    setTweets,
  } = useContext(TweetContext);
  const { loginUserInfo, userLikedTweets, setUserLikedTweets } =
    useContext(InfoContext);

  const handleAddReplyClick = async () => {
    // 
    if (comment.trim().length === 0) {
      setIsEmpty(true);
      return;
    }

    try {
      const replyData = await postReply({ tweetId, comment });

      //  returntoken
      if (replyData.status === "error") {
        return;
      }

      // 
      if (replyData.message === "") {
        Swal.fire({
          position: "top",
          icon: "warning",
          title: replyData.message,
          timer: 1500,
          showConfirmButton: false,
          customClass: {
            icon: "swalIcon right",
            title: "swalTitle",
          },
        });
        return;
      }

      // 
      // 
      setComment("");
      //  modal
      handleReplyClick();
      // 
      Swal.fire({
        position: "top",
        icon: "success",
        title: "",
        timer: 1500,
        showConfirmButton: false,
        customClass: {
          icon: "swalIcon right",
          title: "swalTitle",
        },
      });

      // 
      setTweetReplies([replyData, ...tweetReplies]);
      setTweet({ ...tweet, replyCount: tweet.replyCount + 1 });
      //  tweets 
      setTweets(
        tweets.map((tweet) => {
          if (tweet.id === tweetId) {
            return { ...tweet, replyCount: tweet.replyCount + 1 };
          }
          return tweet;
        })
      );

      //  tweets 
      setUserLikedTweets(
        userLikedTweets.map((tweet) => {
          if (tweet.TweetId === tweetId) {
            return {
              ...tweet,
              Tweet: { ...tweet.Tweet, replyCount: tweet.Tweet.replyCount + 1 },
            };
          }
          return tweet;
        })
      );
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getSingleTweetAsync(tweetId);
    //eslint-disable-next-line
  }, []);

  return (
    <StyledDiv>
      <div className="cross-box">
        <CrossFocus onClick={handleReplyClick} />
      </div>
      <StyledContent className="d-flex">
        <div className="d-flex flex-column me-3">
          <img src={tweet.User?.avatar} alt="avatar" />
          <div className="side-line d-flex justify-content-center align-items-center"></div>
          <img src={loginUserInfo.avatar} alt="" />
        </div>
        <div className="content-area flex-grow-1">
          <div className="d-flex align-items-center title">
            <p className="user-name">{tweet.User?.name}</p>
            <span className="grey">
              @ {tweet.User?.account}  {tweet.createdAt}
            </span>
          </div>
          <p className="tweet-text">{tweet.description}</p>
          <p className="reply-to">
            
            <span className="account">@{tweet.User?.account}</span>
          </p>
          <textarea
            value={comment}
            onChange={(e) => {
              setComment(e.target.value);
              setIsEmpty(false);
            }}
            placeholder=""
            maxLength="140"
          />
          <div className="d-flex justify-content-end align-items-center">
            <p className="hint me-3">
              {isEmpty && <span className="error me-3"></span>}
              {comment.length === 140 && (
                <span className="error me-3">140</span>
              )}
              {comment.length}/140
            </p>
            <Button
              primary
              rounded
              onClick={handleAddReplyClick}
              className="px-4 text-base"
            >
              
            </Button>
          </div>
        </div>
      </StyledContent>
    </StyledDiv>
  );
}

export default ReplyModal;
