import { useContext } from "react";
import styled from "styled-components";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { InfoContext } from "contexts/InfoContext";
import { AdminContext } from "contexts/AdminContext";

// Components
import Logo from "./Logo";
import LinkItem from "./LinkItem";
import Button from "./Button";

const StyledNav = styled.div`
  height: 100vh;
  border-right: 1px solid var(--grey3);
  padding: 20px;

  & .nav {
    width: 178px;
  }

  & .logout {
    margin-top: auto;
  }
`;

function NavBarLinks({ isUser, onTweetClick, status }) {
  const userId = localStorage.getItem("userId");
  const navigate = useNavigate();

  if (isUser) {
    return (
      <>
        <LinkItem
          title=""
          isClick={status === ""}
          onClick={() => navigate("/main")}
        />
        <LinkItem
          title=""
          isClick={status === ""}
          onClick={() => navigate(`/user/${userId}`)}
        />
        <LinkItem
          title=""
          isClick={status === ""}
          onClick={() => navigate("/setting")}
        />

        <Button primary rounded className="w-full" onClick={onTweetClick}>
          
        </Button>
      </>
    );
  }

  return (
    <>
      <LinkItem
        title=""
        isClick={status === ""}
        onClick={() => navigate("/admin/tweets")}
      />
      <LinkItem
        title=""
        isClick={status === ""}
        onClick={() => navigate("/admin/users")}
      />
    </>
  );
}

function NavBar({ isUser, onTweetClick, status }) {
  const { setIsUserLogin } = useContext(InfoContext);
  const { setIsAdminLogin } = useContext(AdminContext);

  const navigate = useNavigate();

  const handleLogoutClick = () => {
    // 
    Swal.fire({
      position: "top",
      icon: "success",
      title: "",
      timer: 1500,
      showConfirmButton: false,
      customClass: {
        icon: "swalIcon right",
        title: "swalTitle",
      },
    });

    // 
    if (localStorage.getItem("adminToken")) {
      setIsAdminLogin(false);
      navigate("/admin");
      localStorage.removeItem("adminToken");
      return;
    }

    // 
    if (localStorage.getItem("token")) {
      setIsUserLogin(false);
      navigate("/login");
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
    }
  };

  return (
    <StyledNav className="col-3 d-flex flex-column align-items-end">
      <div className="nav d-flex flex-column flex-grow-1 ">
        <Logo />
        <div className="navLinks d-flex flex-column justify-content-between flex-grow-1">
          <div>
            <NavBarLinks
              isUser={isUser}
              onTweetClick={onTweetClick}
              status={status}
            />
          </div>
          <div className="logout">
            <LinkItem title="" onClick={handleLogoutClick} />
          </div>
        </div>
      </div>
    </StyledNav>
  );
}

export default NavBar;
