import styled from "styled-components";
import { useContext, useState } from "react";
import Swal from "sweetalert2";
import { postTweet } from "api/tweetAuth";
import { TweetContext } from "contexts/TweetContext";
import { InfoContext } from "contexts/InfoContext";

// Components
import { ReactComponent as CrossFocus } from "assets/icons/cross_focus.svg";
import Button from "components/Button";

const StyledDiv = styled.div`
  width: 600px;
  height: 300px;
  border-radius: 20px;
  background-color: var(--white);

  img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
  }

  // CSS
  .cross-box,
  .content {
    padding: 16px;
  }

  .cross-box {
    border-bottom: 1px solid #e6ecf0;

    svg {
      cursor: pointer;

      &:hover {
        path {
          fill: var(--grey9);
        }
      }
    }
  }

  .content {
    textarea {
      margin: 10px;
      height: 150px;
      resize: none; // 
      border: none; // 
      outline: none; // 

      font-size: 17px;
    }
  }

  .footer {
    p {
      margin-right: 20px;
      color: var(--grey7);
      font-size: 15px;

      span {
        margin-right: 20px;
        color: var(--error);
      }
    }
  }
`;

const TweetModal = () => {
  const [description, setDescription] = useState("");
  const [isEmpty, setIsEmpty] = useState(false);
  const { handleTweetClick, tweets, setTweets } = useContext(TweetContext);
  const { loginUserInfo } = useContext(InfoContext);

  const handleAddTweetClick = async () => {
    // 
    if (description.trim().length === 0) {
      setIsEmpty(true);
      return;
    }

    try {
      const tweetData = await postTweet({ description });

      //  returntoken
      if (tweetData.status === "error") {
        return;
      }

      // 
      if (tweetData.message === "") {
        Swal.fire({
          position: "top",
          icon: "warning",
          title: tweetData.message,
          timer: 1500,
          showConfirmButton: false,
          customClass: {
            icon: "swalIcon right",
            title: "swalTitle",
          },
        });
        return;
      }

      // 
      // 
      setDescription("");
      //  modal
      handleTweetClick();
      // 
      Swal.fire({
        position: "top",
        icon: "success",
        title: "",
        timer: 1500,
        showConfirmButton: false,
        customClass: {
          icon: "swalIcon right",
          title: "swalTitle",
        },
      });

      //  
      setTweets([
        {
          ...tweetData,
          likeCount: 0,
          replyCount: 0,
          User: {
            id: loginUserInfo.id,
            account: loginUserInfo.account,
            name: loginUserInfo.name,
            avatar: loginUserInfo.avatar,
          },
          isLiked: false,
        },
        ...tweets,
      ]);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <StyledDiv className="d-flex flex-column">
      <div className="cross-box">
        <CrossFocus onClick={handleTweetClick} />
      </div>
      <div className="content flex-grow-1">
        <div className="d-flex">
          <img src={loginUserInfo.avatar} alt="" />
          <textarea
            className="flex-grow-1"
            value={description}
            onChange={(e) => {
              setDescription(e.target.value);
              setIsEmpty(false);
            }}
            placeholder=""
            maxLength="140"
          />
        </div>
        <div className="footer d-flex justify-content-end align-items-center">
          <p>
            {isEmpty && <span></span>}
            {description.length === 140 && <span>140</span>}
            {description.length}/140
          </p>
          <Button
            primary
            rounded
            onClick={handleAddTweetClick}
            className="px-4 text-base"
          >
            
          </Button>
        </div>
      </div>
    </StyledDiv>
  );
};

export default TweetModal;
