import styled from "styled-components";
import { useContext, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
import { deleteTweet, getAdminTweets } from "api/adminAuth";
import { AdminContext } from "contexts/AdminContext";

// components
import NavBar from "components/NavBar";
import Header from "components/Header";
import { AdminTweetItem } from "components/TweetItem";

const StyledTweetsDiv = styled.div`
  border-right: 1px solid #e6ecf0;
`;

const StyledTweetsCollection = styled.div`
  width: 100%;
  height: calc(100vh - 75px);
  overflow: auto;
`;

function AdminTweetsPage() {
  const [tweets, setTweets] = useState([]);

  const { isAdminLogin, loginAlert } = useContext(AdminContext);
  const navigate = useNavigate();

  const handleDeleteClick = (id) => {
    // 
    Swal.fire({
      title: "",
      showDenyButton: true,
      confirmButtonText: "",
      denyButtonText: "",
    }).then(async (result) => {
      if (result.isConfirmed) {
        // 
        try {
          const { status, message } = await deleteTweet(id);

          if (status === "success") {
            // 
            setTweets((prevTweets) =>
              prevTweets.filter((tweet) => tweet.id !== id)
            );

            // 
            Swal.fire({
              position: "top",
              icon: "success",
              title: message,
              timer: 1500,
              showConfirmButton: false,
              customClass: {
                icon: "swalIcon right",
                title: "swalTitle",
              },
            });
            return;
          }

          // 
          Swal.fire({
            position: "top",
            icon: "error",
            title: message,
            timer: 1500,
            showConfirmButton: false,
            customClass: {
              icon: "swalIcon right",
              title: "swalTitle",
            },
          });
        } catch (error) {
          console.error(error);
        }
      }
    });
  };

  // useEffect
  // 
  useEffect(() => {
    if (!isAdminLogin) {
      loginAlert();
      navigate("/admin");
    }
  }, [isAdminLogin, loginAlert, navigate]);

  // 
  useEffect(() => {
    const showTweets = async () => {
      const tweetItems = await getAdminTweets();
      setTweets(tweetItems);
    };

    showTweets();
  }, []);

  return (
    <div className="d-flex">
      <NavBar isUser={false} status="" />
      <StyledTweetsDiv className="col-8">
        <Header>
          <h1></h1>
        </Header>
        <StyledTweetsCollection>
          {/*  map  */}
          {tweets.map((tweet) => (
            <AdminTweetItem
              key={tweet.id}
              tweetId={tweet.id}
              avatar={tweet.User.avatar}
              name={tweet.User.name}
              account={tweet.User.account}
              createdAt={tweet.createdAt}
              description={tweet.description}
              onClick={handleDeleteClick}
            />
          ))}
        </StyledTweetsCollection>
      </StyledTweetsDiv>
    </div>
  );
}

export default AdminTweetsPage;
