import { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import Swal from "sweetalert2";
import { putUserSettingInfo } from "api/userAuth";
import { TweetContext } from "contexts/TweetContext";
import { InfoContext } from "contexts/InfoContext";

// components
import MainContainer from "components/containers/MainContainer";
import Header from "components/Header";
import NavBar from "components/NavBar";
import Input from "components/Input";
import ModalContainer from "components/containers/ModalContainer";
import Button from "components/Button";

const StyledDiv = styled.div`
  height: 100vh;
  border-right: 1px solid var(--grey3);
  overflow: auto;
`;

const StyledFormDiv = styled.div`
  padding: 24px;
`;

function SettingPage() {
  const { isTweetModalShow, handleTweetClick } = useContext(TweetContext);
  const {
    isUserLogin,
    loginAlert,
    loginUserId,
    loginUserInfo,
    setLoginUserInfo,
  } = useContext(InfoContext);

  const [account, setAccount] = useState(loginUserInfo.account);
  const [name, setName] = useState(loginUserInfo.name);
  const [email, setEmail] = useState(loginUserInfo.email);
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [whichError, setWhichError] = useState([]);
  const [errorMessage, setErrorMessage] = useState("");

  const navigate = useNavigate();

  const handleAccountChange = (e) => {
    setAccount(e.target.value);
  };

  const handleNameChange = (e) => {
    // 
    setWhichError([]);
    setErrorMessage("");

    //   50 
    if (e.target.value.length > 50) {
      setWhichError(["name"]);
      setErrorMessage(" 50 ");
    }

    setName(e.target.value);
  };

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const handleCheckPasswordChange = (e) => {
    setCheckPassword(e.target.value);
  };

  const handleSaveClick = async (e) => {
    // 
    setWhichError([]);
    setErrorMessage("");

    // 
    if (
      [account, name, email, password, checkPassword].some(
        (value) => value.length === 0
      )
    ) {
      if (account.length === 0) {
        setWhichError((we) => [...we, "account"]);
      }

      if (name.length === 0) {
        setWhichError((we) => [...we, "name"]);
      }

      if (email.length === 0) {
        setWhichError((we) => [...we, "email"]);
      }

      if (password.length === 0) {
        setWhichError((we) => [...we, "password"]);
      }

      if (checkPassword.length === 0) {
        setWhichError((we) => [...we, "checkPassword"]);
      }
      setErrorMessage("");
      // 
      return;
    }

    // 
    if ([account, name].some((value) => value.trim().length === 0)) {
      if (account.trim().length === 0) {
        setWhichError((we) => [...we, "account"]);
      }
      if (name.trim().length === 0) {
        setWhichError((we) => [...we, "name"]);
      }

      setErrorMessage("");
      // 
      return;
    }

    //  50 
    if (name.length > 50) {
      setWhichError(["name"]);
      setErrorMessage(" 50 ");
      return;
    }

    // 
    Swal.fire({
      title: "",
      showDenyButton: true,
      confirmButtonText: "",
      denyButtonText: "",
    }).then(async (result) => {
      if (result.isConfirmed) {
        // 
        try {
          const { message } = await putUserSettingInfo({
            id: loginUserId,
            account,
            name,
            email,
            password,
            checkPassword,
          });

          // 
          if (message === "Account ") {
            setWhichError(["account"]);
            setErrorMessage(message);
            return;
          }

          // name  50 
          if (message === " 50 ") {
            setWhichError(["account"]);
            setErrorMessage(message);
            return;
          }

          // 
          if (message === "Email ") {
            setWhichError(["email"]);
            setErrorMessage(message);
            return;
          }

          // 
          if (message === "Email ") {
            setWhichError(["email"]);
            setErrorMessage(message);
            return;
          }

          // 
          if (message === "") {
            setWhichError(["password", "checkPassword"]);
            setErrorMessage(message);
            return;
          }

          // 
          if (message === "") {
            Swal.fire({
              position: "top",
              icon: "warning",
              title: message,
              timer: 1500,
              showConfirmButton: false,
              customClass: {
                icon: "swalIcon right",
                title: "swalTitle",
              },
            });
            return;
          }

          // 
          Swal.fire({
            position: "top",
            icon: "success",
            title: "",
            timer: 1500,
            showConfirmButton: false,
            customClass: {
              icon: "swalIcon right",
              title: "swalTitle",
            },
          });

          // 
          setPassword("");
          setCheckPassword("");

          // 
          setLoginUserInfo({
            ...loginUserInfo,
            account,
            name,
            email,
          });
        } catch (error) {
          console.error(error);
        }
      }
    });
  };

  // useEffect
  // 
  useEffect(() => {
    if (!isUserLogin) {
      loginAlert();
      navigate("/login");
    }
  }, [isUserLogin, loginAlert, navigate]);

  return (
    <div className="d-flex">
      {isTweetModalShow && <ModalContainer value="" />}
      <NavBar isUser={true} onTweetClick={handleTweetClick} status="" />
      <MainContainer>
        <StyledDiv className="col-8">
          <Header>
            <h1></h1>
          </Header>
          <StyledFormDiv>
            <Input
              id="input_account"
              label=""
              type="text"
              placeholder=""
              value={account}
              onChange={handleAccountChange}
              isError={whichError.some((which) => which === "account")}
              errorMessage={errorMessage}
            />
            <Input
              id="input_name"
              label=""
              type="text"
              placeholder="50"
              value={name}
              onChange={handleNameChange}
              isError={whichError.some((which) => which === "name")}
              errorMessage={errorMessage}
            />
            <Input
              id="input_email"
              label="Email"
              type="text"
              placeholder=" Email"
              value={email}
              onChange={handleEmailChange}
              isError={whichError.some((which) => which === "email")}
              errorMessage={errorMessage}
            />
            <Input
              id="input_password"
              label=""
              type="password"
              placeholder=""
              value={password}
              onChange={handlePasswordChange}
              isError={whichError.some((which) => which === "password")}
              errorMessage={errorMessage}
            />
            <Input
              id="input_checkPassword"
              label=""
              type="password"
              placeholder=""
              value={checkPassword}
              onChange={handleCheckPasswordChange}
              isError={whichError.some((which) => which === "checkPassword")}
              errorMessage={errorMessage}
            />
            <Button
              primary
              rounded
              onClick={handleSaveClick}
              className="px-4 text-base block ml-auto"
            >
              
            </Button>
          </StyledFormDiv>
        </StyledDiv>
      </MainContainer>
    </div>
  );
}

export default SettingPage;
